:root{
  --primary-color: #002256;
  --secondary-color: #f45b14;
  --font-family: Montserrat, sans-serif;
  --gray-color: #eee;
}

*{
  box-sizing: boder-box;
}

html, body {
	font-size: 15px;
  font-family: var(--font-family) !important;
}

body {
  font-family: var(--font-family) !important;
  background-color: white;
  line-height: 1.5;
  text-rendering: optimizespeed;
}

.line-brand::after {
  content: '';
  display: block;
  width: 50px;
  height: 2px;
  background-color: white;
  margin-top:5px;
}

.line-about-us::after {
  content: '';
  display: block;
  width: 50px;
  height: 2px;
  background-color: var(--secondary-color);
  margin-top:5px;
}

@keyframes pulse {
  to {
    box-shadow: 0 0 0 18px #1a76d242;
  }
}